import { Container } from '@mui/system';
import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Count, DarkButton, FlexColumn, FlexRow, Title, EventCards, ProfileIcon, Loading} from '../../style/style';
import { createAPIEndpoint, getDisplayName, getEmail, isLoggedOut } from '../../utils/api';
import NotFound from '../Error/NotFound';

export default function Profile() {
    const navigate = useNavigate();
    const [profilePic, setProfilePic] = useState(null)
    const [events, setEvents] = useState(null)

    useEffect(() => {
        createAPIEndpoint("profile/" + getEmail())
        .get()
        .then(res => {
            setProfilePic(res.data[0].Item.profile_picture.S)
            setEvents(res.data[4])
        })
    }, [])

    const editClick = () => {
        navigate("/profile/edit");
    }

    if(isLoggedOut()){
        return <NotFound />;
    }

    return(
        events === null | profilePic === null ? <Loading />:
        <FlexColumn>
            <Container sx={{ py: 4 }}>
                <FlexRow style={{width: "100%"}}>
                    <ProfileIcon src={profilePic} alt={"profile"} width={200}  height={200}/>
                    <div style={{marginLeft: "10pt"}}>
                        <Title>{getDisplayName()}</Title>
                        <Count>{getEmail()}</Count><br/>
                        <div>
                            <DarkButton style={{marginLeft: 0}}size='sm' onClick={editClick}>Edit</DarkButton>
                        </div>
                    </div>
                </FlexRow>
            </Container>
            <Container sx={{ py: 4 }} maxWidth="sm" maxHeight="sm">
                <EventCards
                    events={events}
                />
            </Container>
        </FlexColumn>
    )
}