import { Card } from '@mui/material';
import { Container } from '@mui/system';
import React, { useEffect, useState }  from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import { DarkButton, DenyButton, FollowItem, FollowerName, Title} from '../../style/style';
import { GREY } from '../../utils/constants';
import { createAPIEndpoint, getEmail } from '../../utils/api';

export default function Members() {
    const { group_id } = useParams(); 
    const [members, setMembers] = useState([]);
    const [xClicked, setXClicked] = useState(false);
    const [clickedUser, setClickedUser] = useState(null);

    const navigate = useNavigate();

    const handleMemberClick = (email) => {
        if(email === getEmail()){
            navigate("/profile")
        } else{
            navigate("/user/" + email)
        }
    };

    const handleXClick = (user) => {
        setXClicked(true);
        setClickedUser(user)
    };
    

    useEffect(() => {
        createAPIEndpoint("memberGroups/group_members/" + group_id)
        .get()
        .then(res => {
            if(res.data.filter(member => {if(member.email.S === getEmail()){return member}}).length === 0){
                navigate("/student-group/" + group_id)
            }
            setMembers(res.data)
        })
        .catch(err => {
            console.log(err);
        })

        if(xClicked) {
            createAPIEndpoint("memberGroups/" + clickedUser + "/" + group_id)
            .delete()
            .then(res => {
                console.log("Deleted")
                setXClicked(false);
                setClickedUser(null)


            })
        }
        
    }, [xClicked])

    return(
        <div style={{flex: "10", display: "flex", flexDirection: "column" }}>
            <DarkButton style={{width: "5%"}} onClick={() => {navigate("/student-group/" + group_id)}}>Back</DarkButton>
            <Title style={{textAlign: "center"}}>Group Members</Title><br/>
            <Container sx={{
                display: 'flex',
                flexDirection: 'column',
                alignItems: "center",
            }}>
                <Card
                    sx={{ 
                        display: 'flex', 
                        flexDirection: 'row',
                        background: GREY,
                        alignItems: "flex-start",
                        px: "10%",
                        py: "10pt"
                    }}
                >
                    <div>
                        {members.map((member) => (
                            <FollowItem>
                                <FollowerName onClick={() => {handleMemberClick(member.email.S)}}>{member.email.S}</FollowerName>
                                <div><DenyButton onClick={() => {handleXClick(member.email.S)}}>X</DenyButton></div>
                            </FollowItem>
                        ))}
                    </div>
                </Card>
            </Container>
        </div>
    )
}