import React, { useEffect, useState } from 'react';
import { MEDIUM_GREEN, WHITE } from '../../utils/constants';
import { AccountMenu, BaseButton, HeaderContainer, Logo } from '../../style/style';
import { useNavigate } from 'react-router-dom';
import { createAPIEndpoint, getDisplayName, getEmail } from '../../utils/api';

export default function Header() {
    const navigate = useNavigate();
    const user = getDisplayName();
    const [studentGroups, setStudentGroups] = useState();

    useEffect(() => {
        if(getEmail()){
            createAPIEndpoint("studentGroups/user/" + getEmail())
            .get()
            .then(res => {
                if(res.status === 200){
                    setStudentGroups(res.data.studentGroups?.Responses.StudentGroups);
                }
            })
            .catch(res => {
                console.log(res);
            })
        }
    }, [user]);

    return(
        <HeaderContainer >
            <Logo to="/home"><span style={{color: WHITE}}>U</span>Events</Logo>
            {user ? 
                <AccountMenu studentGroups={studentGroups} />:
                (<span>
                    <BaseButton onClick={() => {navigate("/login")}}>LOGIN</BaseButton>
                    <BaseButton style={{background: MEDIUM_GREEN}} onClick={() => {navigate("/register")}}>REGISTER</BaseButton>
                </span>)}
        </HeaderContainer>);
}