import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { DarkButton, FlexColumn, Input, Success, UploadPicture, Error } from '../../style/style';
import { getDisplayName, isLoggedOut, setDisplayName, getPhoto, createAPIEndpoint, getEmail, objToFormData, setPhoto, setAuthData } from '../../utils/api';
import NotFound from '../Error/NotFound';

export default function EditProfile() {
    const navigate = useNavigate();
    const [selectedFile, setSelectedFile] = useState(getPhoto());
    const [selectedRawFile, setSelectedRawFile] = useState(null);
    const [passwordError, setPasswordError] = useState(null);
    const [passwordSuccess, setPasswordSuccess] = useState(null);
    const [success, setSuccess] = useState(null);
    const [photoSuccess, setPhotoSuccess] = useState(null);

    const handlePhotoChange = (event) => {
        var file = event.target.files[0];
        setSelectedRawFile(file);
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onloadend = function(e) {
            setSelectedFile(reader.result);
        };
    };

    const editClick = (e) => {
        e.preventDefault();

        setPasswordError(null);
        setPasswordSuccess(null);
        setSuccess(null);
        setPhotoSuccess(null);

        if(e.target.newP.value !== "" & e.target.confirmP.value !== ""){
            if(e.target.newP.value === e.target.confirmP.value){
                let data = {
                    "password": e.target.newP.value
                };

                createAPIEndpoint("change_password/" + getEmail())
                .put(data)
                .then(res => {
                    if(res.status === 200){
                        let authData = window.btoa(getEmail() + ':' + e.target.newP.value);
                        setAuthData(authData)
                        setPasswordSuccess("Successful Password Change")
                    }
                })
                .catch(err => {
                    setPasswordError("Failed to change password")
                })

            } else{
                setPasswordError('Passwords do not match')
            }
        }

        if((e.target.first_name.value + " " + e.target.last_name.value) !== getDisplayName()){
            const data = {
                "email": getEmail(),
                "first_name": e.target.first_name.value,
                "last_name": e.target.last_name.value,
            }

            createAPIEndpoint("profile/edit", data)
            .put(objToFormData(data))
            .then(res => {
                if(res.status === 200){
                    // successfully updated
                    setDisplayName(e.target.first_name.value + " " + e.target.last_name.value);
                    setSuccess("Profile Updated!")
                }
            })
        }

        if(selectedFile !== getPhoto()){
            const photoData = {
                'photo': selectedRawFile,
                'email': getEmail()
            }

            console.log(selectedRawFile)
            createAPIEndpoint("profile/edit_picture", objToFormData(photoData), "", "multipart/form-data")
            .put(objToFormData( photoData))
            .then(res => {
                if(res.status === 200){
                    setPhoto(res.data)
                    setPhotoSuccess("Photo Updated!")
                }
            })
        }
    }

    if(isLoggedOut()){
        return <NotFound />;
    }
    
    return(
            <form style={{flex: "10"}} onSubmit={editClick}>
            <DarkButton onClick={() => {navigate("/profile")}}>Back</DarkButton>
            <FlexColumn>
                <UploadPicture selectedFile={selectedFile} onChangeRaw={handlePhotoChange}/>
                
                {passwordError ? <Error>{passwordError}</Error>: null}
                {passwordSuccess ? <Success>{passwordSuccess}</Success>: null}
                {success ? <Success>{success}</Success>: null}
                {photoSuccess ? <Success>{photoSuccess}</Success>: null}

                <br/>
                <Input type={"text"} id="first_name" name="first_name" defaultValue={getDisplayName().split(" ")[0]}/>
                <br/>

                <Input type={"text"} id="last_name" name="last_name" defaultValue={getDisplayName().split(" ")[1]}/>
                <br/>

                <Input type={"password"} id="newP" name="newP" placeholder='Enter New Password'/>
                <br/>

                <Input type={"password"} id="confirmP" name="confirmP" placeholder='Confirm New Password'/>
                <br/>

                <DarkButton type="submit">SAVE</DarkButton>
            </FlexColumn>
            </form>
    )
}