import React, { useState, useEffect } from 'react';
import Container from '@mui/material/Container';
import { createAPIEndpoint, getEmail, isLoggedOut } from '../../utils/api';
import { Title, SearchCard, Loading, EventSearchCards } from '../../style/style';
import { Grid, Typography } from '@mui/material';

export default function Search() {
    const [users, setUsers] = useState([]);
    const [isUsersLoaded, setIsUsersLoaded] = useState(false);
    const [studentGroups, setStudentGroups] = useState([]);
    const [isStudentGroupsLoaded, setIsStudentGroupsLoaded] = useState(false);
    const [events, setEvents] = useState([]);
    const search = new URLSearchParams(window.location.search).get("search");
    const [isEventsLoaded, setIsEventsLoaded] = useState(false);

    useEffect(() => {
        createAPIEndpoint("profile?search=" + search)
            .get()
            .then((result) => {
                setUsers(result.data);
                setIsUsersLoaded(true);
            })
            .catch((err) => {
                // TODO: Handle error
                console.log(err);
            });
    }, []);

    useEffect(() => {
        createAPIEndpoint("studentGroups?search=" + search)
            .get()
            .then((result) => {
                setStudentGroups(result.data.Items);
                setIsStudentGroupsLoaded(true);
            })
            .catch((err) => {
                // TODO: Handle error
                console.log(err);
            });
    }, []);

    useEffect(() => {
        createAPIEndpoint("events?search=" + search)
            .get()
            .then((result) => {
                setEvents(result.data);
                setIsEventsLoaded(true);
            })
            .catch((err) => {
                // TODO: Handle error
                console.log(err);
            });
    }, []);

    return (
        <Container sx={{ 
            width: '100%',
            height: '100%',
        }}>
            <Grid container spacing={1}
                sx={{
                    display: 'flex',
                    flexDirection: 'row',
                    textAlign: 'center',
                    width: '100%',
                }}
            >
                <Grid item xs={6} sm={6} md={4} lg={4}
                    sx={{
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                    }}
                >
                    <Title>Users</Title>
                    {   
                        !isUsersLoaded ? 
                            <Loading /> :
                            users.length === 0 ? 
                            <Typography variant="h6" sx={{color: 'text.secondary'}}><i>No users found for "{search}"</i></Typography> :
                            users.map((user) => (
                                user.email.S !== getEmail() &&
                                <Container 
                                    key={user.email.S}
                                    sx={{
                                        py: 1,
                                        display: 'flex',
                                        flexDirection: 'column',
                                        alignItems: 'center',
                                    }}
                                >
                                    <SearchCard 
                                        displayName={user.first_name.S + " " + user.last_name.S}
                                        imageUrl={user.profile_picture.S}
                                        link={isLoggedOut() ? `/not-found`:`/user/${user.email.S}`}
                                    />
                                </Container>
                            ))
                    }
                </Grid>
                    
                <Grid item xs={6} sm={6} md={4} lg={4}
                    sx={{
                        display: 'flex',
                        flexDirection: 'column',
                    }}
                >
                    <Title>Student Groups</Title>
                    {   
                        !isStudentGroupsLoaded ? 
                            <Loading /> :
                            studentGroups.length === 0 ? 
                            <Typography variant="h6" sx={{color: 'text.secondary'}}><i>No groups found for "{search}"</i></Typography> : 
                            studentGroups.map((studentGroup) => (
                                <Container 
                                    key={studentGroup.group_id.N}
                                    sx={{
                                        py: 1,
                                        display: 'flex',
                                        flexDirection: 'column',
                                        alignItems: 'center',
                                    }}
                                >
                                    <SearchCard
                                        displayName={studentGroup.group_name.S}
                                        imageUrl={studentGroup.group_photo.S}
                                        link={`/student-group/${studentGroup.group_id.N}`}
                                    />
                                </Container>
                            ))
                    }
                </Grid>

                <Grid item xs={12} sm={12} md={4} lg={4}
                    sx={{
                        display: 'flex',
                        flexDirection: 'column',
                    }}
                >
                    <Title>Events</Title>
                    {
                        !isEventsLoaded ? 
                            <Loading /> :
                            events.length === 0 ?
                                <Typography variant="h6" sx={{color: 'text.secondary'}}><i>No events found for "{search}"</i></Typography> :
                                <EventSearchCards events={events}/>
                    }
                </Grid>
            </Grid>
        </Container>
    )
}