import { CardMedia, Grid } from '@mui/material';
import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Count, DarkButton, BorderContainer, FlexColumn, FlexRow, Loading, Title, RSVPCount, ShadowBorder, EventImage} from '../../style/style';
import { GoLocation } from 'react-icons/go';
import { useParams } from 'react-router-dom';
import { createAPIEndpoint, getEmail, isLoggedOut } from '../../utils/api';
import Dayjs from 'dayjs';
import { MEDIUM_GREEN } from '../../utils/constants';

export default function Event() {
    const { event_id } = useParams();
    const [rsvp, setRsvp] = useState(false);
    const [rsvpCount, setRsvpCount] = useState(null);
    const [studentGroup, setStudentGroup] = useState({
        group_id: null,
        group_name: null,
    });
    // set as null so that it will actually show the loading spinner instead  of mocked data
    const [eventEntity, setEventEntity] = useState(null);
    const navigate = useNavigate();

    useEffect(() => {
        createAPIEndpoint("events/" + event_id)
            .get()
            .then((result) => {
                setEventEntity({
                    ...eventEntity,
                    "title": result.data.event_name.S,
                    "description": result.data.event_description.S,
                    "location": result.data.event_location.S,
                    "photo": result.data.event_photo.S,
                    "dateTime": result.data.event_date_time.S,
                });

                setStudentGroup({
                    ...studentGroup,
                    "group_id": result.data.event_coordinator_group_id.N,
                })
            })
            .catch((err) => {
                // TODO: Handle error
                console.log(err)
            });

        createAPIEndpoint("events/RSVP/" + event_id)
            .get()
            .then(res => {
                if(res.status === 200){
                    setRsvpCount(res.data.length)
                }
            })
    }, [rsvp])

    useEffect(() => {
        if (studentGroup.group_id) {
            createAPIEndpoint("studentGroups/" + studentGroup.group_id)
                .get()
                .then((result) => {
                    setStudentGroup({
                        ...studentGroup,
                        "group_name": result.data.group_name.S,
                    });
                })
                .catch((err) => {
                    // TODO: Handle error
                    console.error(err);
                });
        }
    }, [studentGroup.group_id])
    
    useEffect(() => {
        createAPIEndpoint("events/RSVP/" + event_id + "/" + getEmail())
            .get()
            .then((result) => {
                console.log(result.data);
                setRsvp(result.data);
            })
            .catch((err) => {
                // TODO: Handle error
                console.error(err);
            });
    }, [])

    const rsvpClick = () => {
        if (rsvp) {
            createAPIEndpoint("events/RSVP/" + event_id + "/" + getEmail())
                .delete()
                .then((_result) => {
                    setRsvp(false);
                })
                .catch((err) => {
                    // TODO: Handle error
                    console.error(err);
                })
        }
        else {
            const payload = {
                email: getEmail(),
            };
            createAPIEndpoint("events/RSVP/" + event_id)
                .post(payload)
                .then((_result) => {
                    setRsvp(true);
                })
                .catch((err) => {
                    // TODO: Handle error
                    console.error(err);
                })
        }
    }

    const handleGroupClick = () => {
        navigate("/student-group/" + studentGroup.group_id);
    }

    return(
        eventEntity === null | studentGroup.group_id === null | studentGroup.group_name === null? <Loading />:
        <div  style={{flex: "10", display: "flex", flexDirection: "column"}}>
            <DarkButton style={{flex: "0", width: "0%"}}onClick={() => {navigate("/home")}}>Back</DarkButton>
            <FlexColumn >
                <ShadowBorder>
                    <FlexRow style={{justifyContent: "flex-start"}}>
                        <EventImage style={{backgroundImage: `url(${eventEntity.photo})`}}> 
                        {isLoggedOut() ? null: <RSVPCount count={rsvpCount} />}
                        </EventImage>

                        <div style={{marginLeft: "5%"}}>
                            <Title>{eventEntity.title}</Title>
                            <Count>Hosted by <span style={{cursor: "pointer", fontWeight: "500"}} onClick={handleGroupClick}>{studentGroup.group_name}</span> </Count><br/>
                            {eventEntity.description}
                            <br/><br/>
                            <span>
                                <GoLocation size={"24px"}/>
                                {eventEntity.location}
                            </span>
                            <br/>
                            {Dayjs(eventEntity.dateTime).format("dddd, MMMM D, YYYY h:mm A")}
                            <div>
                                {isLoggedOut() ? null: <DarkButton style={{marginLeft: "0"}}size='sm' onClick={rsvpClick}>{rsvp ? "UNRSVP": "RSVP"}</DarkButton>}
                            </div>
                        </div>
                    </FlexRow>
                </ShadowBorder>
            </FlexColumn>
        </div>
    )
}