import { Container } from '@mui/system';
import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { Count, FlexColumn, FlexRow, Title, EventCards, CenterText, ProfileIcon, Loading, LightButton} from '../../style/style';
import { createAPIEndpoint, getEmail, isLoggedOut } from '../../utils/api';
import { WHITE } from '../../utils/constants';
import NotFound from '../Error/NotFound';

export default function User() {
    const [followStatus, setFollowStatus] = useState(false);
    const { user_id } = useParams();
    const [events, setEvents] = useState(null)
    const [approved, setApproved] = useState(false)
    const [userDetails, setUserDetails] = useState(null)
    const [requestFollow, setRequestFollow] = useState(undefined)
    const [requestUnFollow, setRequestUnFollow] = useState(undefined)
    const [fetchUser, setFetchUser] = useState(false);

    let params = {"user": getEmail()}
    let payload = {"follower_email": getEmail(), "followee_email": user_id }

    const followClick = (e) => {
        if(e.target.innerHTML.includes("UNFOLLOW") | e.target.innerHTML.includes("PENDING")){
            // we want to request unfollow
            console.log("unfollow");
            setRequestUnFollow(true)
        } else if(e.target.innerHTML.includes("FOLLOW") ){
            // we want to request follow
            console.log("request follow")
            setRequestFollow(true);
        }
    };

    useEffect(() => {
        createAPIEndpoint("profile/" + user_id, "", params)
            .get()
            .then((res) => {
                setUserDetails(res.data[0].Item)
                setApproved(res.data[res.data.length-1])
                setFollowStatus(res.data[res.data.length-2])
                
                if(res.data[res.data.length - 1] & res.data[res.data.length - 2]){
                    setEvents(res.data[res.data.length - 3])
                }

                setFetchUser(false)
                setRequestUnFollow(false);
                setRequestFollow(false);
            })
            .catch(err => {
                console.log(err)
                return(<NotFound />);
            })
    }, [user_id, fetchUser]);

    useEffect(() => {
        if(requestFollow === true){
            console.log("sending follow request")
             createAPIEndpoint("profile/follow")
            .post(payload)
            .then(res => {
                if(res.status === 200){
                    setFetchUser(true);
                }else{
                    console.log(res.status)
                }
                console.log(res.data)

            })
            .catch(err => {
                console.log(err);
            })
        }
    }, [requestFollow])

    useEffect(() => {
        if(requestUnFollow === true){
            console.log("sending unfollow request")
            createAPIEndpoint("profile/unfollow", payload)
            .delete()
            .then(res => {
                if(res.status === 200){
                    setFetchUser(true);
                } else{
                    console.log(res.status)
                }
                console.log(res.data)
            })
            .catch(err => {
                console.log(err);
            })
        }
    }, [requestUnFollow])

    if(isLoggedOut()){
        return <NotFound />;
    }

    return(
        userDetails === null & events === null ? <Loading />:
        <FlexColumn>
            <Container sx={{ py: 4 }}>
                <FlexRow style={{width: "100%"}}>
                    <ProfileIcon src={userDetails.profile_picture?.S} alt={"profile"} width={200}  height={200}/>
                    <div style={{marginLeft: "10pt"}}>
                        <Title>{userDetails.first_name?.S + " " + userDetails.last_name?.S}</Title>
                        {followStatus && approved ? <div><Count>{userDetails.email?.S}</Count><br/></div>: null}
                        <div style={{color: WHITE}}>
                            <LightButton loading={requestFollow | requestUnFollow} style={{marginLeft: "0%"}}size='sm' onClick={followClick}>{followStatus ? approved? "UNFOLLOW":"PENDING": "FOLLOW"}</LightButton>
                        </div>
                    </div>
                </FlexRow>
            </Container>
            
            {followStatus && approved? 
            <CenterText>
                <Title>RSVPd Events!</Title>
                <Container sx={{ py: 4 }} maxWidth="sm" maxheight="sm">
                    {events.length > 0? 
                    <EventCards
                        events={events}
                    />: <div>No RSVPd events just yet</div>}
                </Container>
            </CenterText>:null}
        </FlexColumn>
    )
}