import { Container } from '@mui/system';
import React from 'react';
import { useNavigate } from 'react-router-dom';
import { CenterText, DarkButton, ECRequestCard, Title } from '../../style/style';
import { isAdmin, isLoggedOut } from '../../utils/api';
import NotFound from '../Error/NotFound';

export default function ECRequests() {
    const navigate = useNavigate();
    
    if(isLoggedOut() | !isAdmin()){
        return <NotFound />
    }
    return(
        <div style={{flex: 10}}>
            <DarkButton style={{width: "5%"}} onClick={() => {navigate("/profile")}}>Back</DarkButton>
        <div style={{ display: "flex", flexDirection: "column", alignItems: "center", }}>
            <Title style={{textAlign: "center"}}>Event Coordinator Requests</Title>
            Current Event Coordinator Requests Waiting Your Approval
            <CenterText>
                <Container sx={{ py: 4 }} maxWidth="sm" maxheight="sm">
                    <ECRequestCard />
                </Container>
            </CenterText>
        </div>
        </div>
    )
}