export const FilterButton = ({showFilters, selectedFilterOptions, toggleFilters}) => {
    return (
        <Button 
            sx={{ 
                color: (showFilters || selectedFilterOptions.length > 0) ? WHITE : BLACK, 
                background: (showFilters || selectedFilterOptions.length > 0) ? DARK_GREEN : GREY,
                '&:hover': {
                    backgroundColor: DARK_GREEN,
                    color: WHITE
                },
            }} 
            startIcon={<TuneIcon />} 
            variant="contained" 
            onClick={toggleFilters}
        >
            Filter
        </Button> 
    );
};

