export const ECRequestCard = () => {
    const [denyClicked, setDenyClicked] = useState(false);
    const [acceptClicked, setAcceptClicked] = useState(false);
    const [requests, setRequests] = useState(null);

    const handleDeny = (request) => {
        let data = {
            "group_id": request.group_id.N,
            "email": request.email.S
        }

        createAPIEndpoint("requests/reject")
        .put(data)
        .then(res => {
            if(res.status === 200){
                setDenyClicked(true);
            }
            
        })
        .catch(err => {
            console.error(err);
        })

    }
    const handleAccept = (request) => {
        let data = {
            "group_id": request.group_id.N,
            "email": request.email.S
        }

        createAPIEndpoint("requests/approve")
        .put(data)
        .then(res => {
            if(res.status === 200){
                setAcceptClicked(true);
            }
            
        })
        .catch(err => {
            console.error(err);
        })
        setAcceptClicked(true);

    }

    useEffect(() => {
        createAPIEndpoint("requests/pending")
        .get()
        .then(res => {
            setRequests(res.data.Items);
            setDenyClicked(false);
            setAcceptClicked(false);


        })
    }, [denyClicked, acceptClicked])

    return (
        !requests ? <Loading/>:

        <Grid container spacing={4}>
            {
                requests.map((request) => (
                    <Grid item key={request.group_id.N} xs={12} sm={12} md={12}>
                        <Card
                            sx={{ 
                                display: 'flex', 
                                flexDirection: 'row',
                                border: `2pt solid ${MEDIUM_GREEN}`,
                                boxShadow: `3pt 3pt ${MEDIUM_GREEN}`
                            }}
                        >
                            <Container sx={{
                                display: 'flex',
                                flexDirection: 'column',
                                alignItems: "center",
                            }}>
                                <CardContent sx={{ flexGrow: 1,  }}>
                                    <Typography gutterBottom variant="h5" component="h2" color={DARK_GREEN}>
                                        {request.group_name.S}: {request.group_id.N}
                                    </Typography>
                                    {request.email.S}<br/>
                                    <Count>{request.description.S}</Count>
                                </CardContent>
                                <CardActions>
                                    <DarkButton onClick={() => {handleAccept(request)}} size="small">Accept</DarkButton>
                                    <LightButton onClick={() => {handleDeny(request)}} size="small">Deny</LightButton>
                                </CardActions>
                            </Container>
                        </Card>
                    </Grid>
                ))
            }
        </Grid>
    );
};

export function AccountMenu({studentGroups}) {
    const [anchorEl, setAnchorEl] = React.useState(null);
    const navigate = useNavigate();
    const open = Boolean(anchorEl);

    const handleClick = (event) => {
      setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
      setAnchorEl(null);
    };


    return (
      <React.Fragment>
        <Box sx={{ display: 'flex', alignItems: 'center', textAlign: 'center' }}>
          <Tooltip title="Account settings">
            <IconButton
              onClick={handleClick}
              size="small"
              aria-controls={open ? 'account-menu' : undefined}
              aria-haspopup="true"
              aria-expanded={open ? 'true' : undefined}
            >
                <ProfileIcon src={getPhoto()} width="50" height="50"/>
            </IconButton>
          </Tooltip>
        </Box>
        <Menu 
            anchorEl={anchorEl}
            id="account-menu"
            open={open}
            onClose={handleClose}
            onClick={handleClose}
            PaperProps={{
                sx: {
                    '& .MuiAvatar-root': {
                        width: 20,
                        height: 20,
                        mr: 0.5,
                    },
                    borderRadius: "10pt"
                }
            }}
        >
          <DropdownItem  onClick={() => {navigate('/profile')}}>
            <Avatar /> Profile
          </DropdownItem>
          <DropdownItem onClick={() => {

                clearCookies();

                navigate("/home")
            }}>
                <Logout style={{marginRight: "2pt"}} fontSize="small"/> Logout
          </DropdownItem>
          <DropdownItem onClick={() => {navigate('/follow')}}>
            <Loyalty /> Connections
          </DropdownItem>
          {isAdmin() ?
            <DropdownItem onClick={() => {navigate("/profile/ec-requests")}}>
                <GroupAdd /> Student Group Requests
            </DropdownItem>: null} 
            
          <Divider />

          {studentGroups?.length > 0 ? 
            <div>
                {studentGroups.map((group, ind) => (
                    <DropdownItem key={ind} onClick={() => {navigate("/student-group/" + group.group_id.N)}}>
                        {group.group_name.S}
                    </DropdownItem>
                ))}
            </div>: null}
            <DropdownItem onClick={() => {navigate("/profile/ec-register")}}>
                Register as Event Coordinator
            </DropdownItem>
        </Menu>
      </React.Fragment>
    );
}

export function MembersDropdown({group_id}) {
    const [anchorEl, setAnchorEl] = React.useState(null);
    const [members, setMembers] = useState([]);
    const [xClicked, setXClicked] = useState(false);
    const [clickedUser, setClickedUser] = useState(null);
    const navigate = useNavigate();
    const open = Boolean(anchorEl);

    const handleClick = (event) => {
      setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
      setAnchorEl(null);
    };

    const handleMemberClick = (email) => {
        if(email === getEmail()){
            navigate("/profile")
        } else{
            navigate("/user/" + email)
        }
    };

    const handleXClick = (user) => {
        setXClicked(true);
        setClickedUser(user)
    };

    useEffect(() => {
        createAPIEndpoint("memberGroups/group_members/" + group_id)
        .get()
        .then(res => {
            if(res.data.filter(member => {if(member.email.S === getEmail()){return member}}).length === 0){
                navigate("/student-group/" + group_id)
            }
            setMembers(res.data)
        })
        .catch(err => {
            console.log(err);
        })

        if(xClicked) {
            createAPIEndpoint("memberGroups/" + clickedUser + "/" + group_id)
            .delete()
            .then(res => {
                setXClicked(false);
                setClickedUser(null);
                if(clickedUser === getEmail()){
                    navigate("/profile")
                } 
            })
        }
        
    }, [xClicked])

    return (
      <React.Fragment>
        <Box sx={{ display: 'flex', alignItems: 'center', textAlign: 'center' }}>
          <Tooltip title="Members">
            <IconButton
              onClick={handleClick}
              size="small"
              aria-controls={open ? 'account-menu' : undefined}
              aria-haspopup="true"
              aria-expanded={open ? 'true' : undefined}
            >
                <DarkButton style={{margin: "0 0 0 0"}} >MEMBERS</DarkButton>
            </IconButton>
          </Tooltip>
        </Box>
        <Menu 
            anchorEl={anchorEl}
            id="member-menu"
            open={open}
            onClose={handleClose}
            onClick={handleClose}
            PaperProps={{
                sx: {
                    borderRadius: "10pt",
                    display: "flex",
                    alignItems: "center"
                }
            }}
        >
            {members.map((member) => (
                <DropdownItem sx={{display: "flex", justifyContent: "space-between", }}>
                    <span style={{marginRight: "10pt"}} onClick={() => {handleMemberClick(member.email.S)}}>{member.email.S}</span>
                    <div><DenyButton onClick={() => {handleXClick(member.email.S)}}>X</DenyButton></div>
                </DropdownItem>
            ))}
        </Menu>
      </React.Fragment>
    );
}

