import React, { useContext, useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { DarkButton, FlexColumn, Input, LargeTextArea, UploadPicture, BasicDateTimePicker, EventTagSelects } from '../../style/style';
import AppContext from '../../utils/context/context';
import Dayjs from 'dayjs';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import Select from '@mui/material/Select';
import { createAPIEndpoint, getEmail, objToFormData } from '../../utils/api';
import Container from '@mui/material/Container';
import { eventFilterOptions } from '../../utils/constants';
import FormControl from '@mui/material/FormControl';
import { useParams } from 'react-router-dom';

export default function CreateEvent() {
    const navigate = useNavigate();
    const { group_id } = useParams();
    const [context] = useContext(AppContext);
    const [selectedFile, setSelectedFile] = useState(context.photo);
    const [selectedRawFile, setSelectedRawFile] = useState(null);
    const [selectedDateTime, setSelectedDateTime] = useState(new Dayjs());
    const [studentGroups, setStudentGroups] = useState([]);
    const [selectedStudentGroupId, setSelectedStudentGroupId] = useState("0");
    const [eventTags, setEventTags] = useState([]);
    const [eventEntity, setEventEntity] = useState({
        title: "Title",
        description: "Description",
        location: "Location"
    });

    useEffect(() => {
        createAPIEndpoint("studentGroups/user/" + getEmail())
            .get()
            .then(res => {
                setStudentGroups(res.data.studentGroups.Responses.StudentGroups);
            }) 
    }, [context.email])

    const handleEventEntityChange = (event) => {
        setEventEntity({
            ...eventEntity,
            [event.target.name]: event.target.value
        });
    };

    const handleSelectChange = (event) => {
        setSelectedStudentGroupId(event.target.value);
    };

    const handleDateTimeChange = (newDateTime) => {
        setSelectedDateTime(newDateTime);
    };

    const handlePhotoChange = (event) => {
        var file = event.target.files[0];
        setSelectedRawFile(file);
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onloadend = function(e) {
            setSelectedFile(reader.result);
        };
    };

    const createClick = (e) => {
        e.preventDefault();

        let payload = {
            ...eventEntity,
            'studentGroup': selectedStudentGroupId,
            'dateTime': selectedDateTime.format(),
            'photo': selectedRawFile,
            'email': context.email || getEmail(),
            'eventTags': eventTags
        }

        createAPIEndpoint("events", "", "", 'multipart/form-data')
            .post(objToFormData(payload))
            .then(res => {
                console.log(res);
                navigate("/student-group/" + group_id);
            })
            .catch(err => {
                //TODO: handle error
                console.log(err);
            })
    }

    return(
        <div style={{flex: "10", display: "flex", flexDirection: "column"}} >
            <DarkButton style={{width: "5%"}} onClick={() => {navigate("/student-group/" + group_id)}}>Back</DarkButton>
            <div style={{display: "flex", flexDirection: "column", justifyContent: "center",flex: "10" }}>
                <form  onSubmit={createClick}>
                    <FlexColumn>
                        <UploadPicture selectedFile={selectedFile} /**onChange={setSelectedFile}*/ onChangeRaw={handlePhotoChange}/>
                        <Input type={"text"} id="title" name="title" defaultValue={eventEntity.title} onChange={handleEventEntityChange}/>
                        <br/>

                        <Input type={"text"} id="location" name="location" defaultValue={eventEntity.location} onChange={handleEventEntityChange}/>
                        <br/>

                        <Container sx={{
                            display: 'flex',
                            flexDirection: 'row',
                            justifyContent: 'center',
                            alignItems: 'center'
                        }}>
                            <FormControl>
                                <InputLabel id="sg-select-label">Student Group</InputLabel>
                                <Select
                                    labelId="sg-select-label"
                                    id="sg-select"
                                    value={selectedStudentGroupId}
                                    label="Student Group"
                                    onChange={handleSelectChange}
                                >
                                    {studentGroups.map((studentGroup) => {
                                        return <MenuItem value={studentGroup.group_id.N}>{studentGroup.group_name.S}</MenuItem>
                                    })}
                                </Select>
                            </FormControl>
                            
                            <EventTagSelects tags={eventFilterOptions} selectedTags={eventTags} setSelectedTags={setEventTags} />
                        </Container>

                        <BasicDateTimePicker id="datetime" selectedDateTime={selectedDateTime} handleDateTimeChange={handleDateTimeChange}/>

                        <LargeTextArea id="description" name="description" minRows={4} defaultValue={eventEntity.description} onChange={handleEventEntityChange}/>

                        <DarkButton type="submit">CREATE</DarkButton>
                    </FlexColumn>
                </form>
            </div>
        </div>
    )
}