import axios from 'axios'
import Cookies from 'js-cookie'

const BASE_URL = (process.env.NODE_ENV === 'production') ? 'http://13.59.43.80:8000' : 'http://127.0.0.1:8000';

export const BASIC_AUTH_COOKIE_NAME = 'X-UEVENTS-BASIC-AUTH';
export const DISPLAYNAME_COOKIE_NAME = 'X-UEVENTS-USERNAME';
export const EMAIL_COOKIE_NAME = 'X-UEVENTS-EMAIL';
export const PHOTO_COOKIE_NAME = 'X-UEVENTS-PHOTO';
export const PHOTO_COOKIE_ADMIN = 'X-UEVENTS-ADMIN';

export const ENDPOINTS = {
    // populate later
}

export const createAPIEndpoint = (endpoint, data="", params="", contentType="") => {
    let url = BASE_URL + '/api/' + endpoint

    const requestOptions = {
        headers: authHeader(contentType),
        credentials: "same-origin",
    }

    if (data) {
        requestOptions.data = data;
    }

    if (params) {
        requestOptions.params = params;
    }

    return {
        post: data => axios.post(url, data, requestOptions),
        put: data => axios.put(url, data, requestOptions),
        get: () => axios.get(url, requestOptions),
        delete: () => axios.delete(url, requestOptions),
    }
}

function authHeader(contentType) {
    // return authorization header with basic auth credentials
    let authData = Cookies.get(BASIC_AUTH_COOKIE_NAME);
    if (authData) {
        return { 
            'Authorization': 'Basic ' + authData,
            'Content-Type': contentType ? contentType : 'application/json',
        };
    }
}

export function objToFormData(payload) {
    const formData = new FormData();
    for (const [key, value] of Object.entries(payload)) {
        if (value instanceof File || value instanceof Blob) {
            formData.append(key, value);
        } 
        else if (typeof value === 'object') {
            formData.append(key, JSON.stringify(value));
        } 
        else {
            formData.append(key, value);
        }
    }
    return formData;
}

export function setCookies(authData, displayName, email, photo, admin) {
    Cookies.set(BASIC_AUTH_COOKIE_NAME, authData)
    Cookies.set(DISPLAYNAME_COOKIE_NAME, displayName)
    Cookies.set(EMAIL_COOKIE_NAME, email)
    Cookies.set(PHOTO_COOKIE_NAME, photo)
    Cookies.set(PHOTO_COOKIE_ADMIN, admin)
}

export function setDisplayName(displayName) {
    Cookies.set(DISPLAYNAME_COOKIE_NAME, displayName)
}

export function getDisplayName() {
    return Cookies.get(DISPLAYNAME_COOKIE_NAME);
}

export function getEmail() {
    return Cookies.get(EMAIL_COOKIE_NAME);
}

export function getPhoto() {
    return Cookies.get(PHOTO_COOKIE_NAME);
}

export function isAdmin() {
    return Cookies.get(PHOTO_COOKIE_ADMIN) === "true";
}

export function setPhoto(photo) {
    Cookies.set(PHOTO_COOKIE_NAME, photo)
}

export function setAuthData(authData){
    Cookies.set(BASIC_AUTH_COOKIE_NAME, authData)
}

export function clearCookies() {
    Cookies.remove(BASIC_AUTH_COOKIE_NAME)
    Cookies.remove(DISPLAYNAME_COOKIE_NAME)
    Cookies.remove(EMAIL_COOKIE_NAME)
    Cookies.remove(PHOTO_COOKIE_NAME)
    Cookies.remove(PHOTO_COOKIE_ADMIN)
}

export function isLoggedOut() {
    if(getEmail()){
        return false;
    }
    return true
}