import React, { useState } from 'react';
import { Divider, BorderContainer, Error, FlexRow, Input, SubmitButton, Title } from '../../style/style';
import { useNavigate } from 'react-router-dom';
import { createAPIEndpoint, setCookies } from '../../utils/api';

export default function Register() {
    const navigate = useNavigate();
    const [error, setError] = useState(undefined);

    const authenticate = (e) => {
        e.preventDefault();

        // check for matching password
        if(e.target.password.value !== e.target.confirm.value) {
            setError("Passwords do not match")
        } else{

        let payload = {
            "email": e.target.email.value,
            "first_name": e.target.first_name.value,
            "last_name": e.target.last_name.value,
            "password": e.target.password.value,
        }

        createAPIEndpoint("signup")
            .post(payload)
            .then(res => {
                // store the user's data in cookies
                let authData = window.btoa(e.target.email.value + ':' + e.target.password.value);
                
                setCookies(authData, res.data.first_name.S + " " + res.data.last_name.S , res.data.email.S, res.data.profile_picture.S);

                navigate("/home")
            })
            .catch(err => {
                setError(err.response.data)
                console.log(err)
            })
        }
    }

    return(
    <FlexRow>
        <BorderContainer onSubmit={authenticate}>
            <Title> Register Account</Title>
            <Divider />
            {error ? <Error>{error}</Error>: null}
            <br/>
            <Input type={"text"} id="email" name="email" placeholder='Email' required/>
            <br/>
            <Input type={"text"} id="first_name" name="first_name" placeholder='First Name' required/>
            <br/>
            <Input type={"text"} id="last_name" name="last_name" placeholder='Last Name' required/>
            <br/>
            <Input type={"password"} id="password" name="password" placeholder='Password' required/>
            <br/>
            <Input type={"password"} id="confirm" name="confirm" placeholder='Confirm Password' required/>
            <br/><br/>
            <SubmitButton type="submit">Register</SubmitButton>

        </BorderContainer>
    </FlexRow>
    );
}

