import React, { useEffect, useState }  from 'react';
import { useNavigate } from 'react-router-dom';
import { AcceptButton, DarkButton, DenyButton, FollowerName, FollowItem, FollowSelector, Loading } from '../../style/style';
import { GREY } from '../../utils/constants';
import { Checkmark } from 'react-checkmark'
import { Container } from '@mui/system';
import { Card, Divider } from '@mui/material';
import { createAPIEndpoint, getEmail, isLoggedOut } from '../../utils/api';
import NotFound from '../Error/NotFound';

export default function Follow() {
    const navigate = useNavigate();
    const [followType, setFollowType] = useState('followers');
    const [followers, setFollowers] = useState(null);
    const [followings, setFollowings] = useState(null);
    const [followingGroups, setFollowingGroups] = useState([]);
    const [requests, setRequests] = useState(null);
    const [xClicked, setXClicked] = useState(false);
    const [checkClicked, setCheckClicked] = useState(false);
    const [groupXClicked, setGroupXClicked] = useState(null);

    const [otherUser, setOtherUser] = useState(null);

    const handleFollowTypeChange = (newFollowType) => {
        setFollowType(newFollowType)
    };

    const handleXClick =  (otherUser) => {
        setXClicked(true);
        setOtherUser(otherUser);
    };

    const handleCheckClick = (otherUser) => {
        setCheckClicked(true);
        setOtherUser(otherUser);
    };

    const handleGroupUnfollow = (group_id) => {
        setGroupXClicked(group_id)
    }

    useEffect(() => {
        createAPIEndpoint("profile/" + getEmail(), true)
        .get()
        .then(res => {
            setRequests(res.data[1])
            setFollowers(res.data[2])
            setFollowings(res.data[3])
        })
        .catch(err => {
            console.log(err);
            return(<NotFound />)
        })
    }, [xClicked, checkClicked])

    useEffect(() => {
        if(xClicked){
            let payload;
            followType === "followers" ? 
            payload = {"follower_email": otherUser, "followee_email": getEmail() }:
            payload = {"follower_email": getEmail(), "followee_email": otherUser }

            createAPIEndpoint("profile/unfollow", payload)
            .delete()
            .then(res => {
                setXClicked(false);
                setOtherUser("");
            })
            .catch(err => {
                console.log(err);
            })
        }
    }, [xClicked])

    useEffect(() => {
        if(checkClicked){
            let payload = {"follower_email": otherUser, "followee_email": getEmail()}
            createAPIEndpoint("profile/accept")
            .post(payload)
            .then(res => {
                setCheckClicked(false);
                setOtherUser("");
            })
            .catch(err => {
                console.log(err);
            })
        }
    }, [checkClicked])

    useEffect(() => {
        createAPIEndpoint("followGroup/user/" + getEmail() + "/names")
        .get()
        .then(res => {
            setFollowingGroups(res.data.Responses.StudentGroups)
        })

        if(groupXClicked){
            let data = {
                "follower_email": getEmail()
            }
            createAPIEndpoint("followGroup/" + groupXClicked + "/unfollow", data)
            .delete()
            .then(res => {
                setGroupXClicked(false)
            })
        }
    }, [groupXClicked])

    
    if(isLoggedOut()){
        return <NotFound />;
    }

    return(
        <div style={{flex: "10", display: "flex", flexDirection: "column" }}>
            <DarkButton style={{width: "5%"}} onClick={() => {navigate("/profile")}}>Back</DarkButton>

            <Container sx={{
                display: 'flex',
                flexDirection: 'column',
                alignItems: "center",
            }}>
                <FollowSelector
                    followType={followType}
                    onChange={handleFollowTypeChange}
                />
                {followers === null & followings === null & requests === null ? <Loading />:
                <Card
                    sx={{ 
                        display: 'flex', 
                        flexDirection: 'row',
                        background: GREY,
                        alignItems: "flex-start",
                        p: "10pt"
                    }}
                >
                {followType === "followers" ? 
                    <div>
                        { requests.Items?.map((request) => (
                            <FollowItem item key={request.follower_email.S}>
                                <FollowerName  onClick={() => { navigate("/user/" + request.follower_email.S)}}>{request.follower_email.S}</FollowerName>
                                <div>
                                    <AcceptButton onClick={() => {handleCheckClick(request.follower_email.S)}}><Checkmark size={"medium"}/></AcceptButton>
                                    <DenyButton onClick={() => {handleXClick(request.follower_email.S)}}>X</DenyButton>
                                </div>
                            </FollowItem>
                        ))}
                        <Divider sx={{m: "10pt"}}/>
                        {followers.Items?.map((follower) => (
                            <FollowItem item key={follower.follower_email.S}>
                                <FollowerName  onClick={() => { navigate("/user/" + follower.follower_email.S)}}>{follower.follower_email.S}</FollowerName>
                                <div ><DenyButton onClick={() => {handleXClick(follower.follower_email.S)}} >X</DenyButton></div>
                            </FollowItem>
                        ))}
                    </div>:
                    <div style={{width: "100%"}}>
                        {followingGroups?.map(group => (
                            <FollowItem item key={group.group_id.N}>
                                <FollowerName  id={group.group_id.N} onClick={() => { navigate("/student-group/" + group.group_id.N)}}>{group.group_name.S}</FollowerName>
                                <div><DenyButton onClick={() => {handleGroupUnfollow(group.group_id.N)}}>X</DenyButton></div>
                            </FollowItem>
                        ))}
                        {followings.Items?.map((following) => (
                            <FollowItem item key={following.followee_email.S}>
                                <FollowerName  onClick={() => { navigate("/user/" + following.followee_email.S)}}>{following.followee_email.S}</FollowerName>
                                <div><DenyButton onClick={() => {handleXClick(following.followee_email.S)}}>X</DenyButton></div>
                            </FollowItem>
                        ))}
                    </div>}
                </Card>}
            </Container>
        </div>
    )
}