import React, { useContext, useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { DarkButton, FlexColumn, Input, LargeTextArea, UploadPicture, BasicDateTimePicker, DeleteButton, EventTagSelects } from '../../style/style';
import { Checkbox, FormControlLabel, Box } from '@mui/material';
import AppContext from '../../utils/context/context';
import { useParams } from 'react-router-dom';
import { createAPIEndpoint, objToFormData } from '../../utils/api';
import { eventFilterOptions } from '../../utils/constants';
import Dayjs from 'dayjs';

export default function EditEvent() {
    const navigate = useNavigate();
    const { group_id } = useParams();

    const [notification, setNotification] = useState(false);
    const [context, setContext] = useContext(AppContext);
    const [selectedDateTime, setSelectedDateTime] = useState(new Dayjs());
    const [selectedRawFile, setSelectedRawFile] = useState(null);
    const [selectedFile, setSelectedFile] = useState(context.photo);
    const [eventTags, setEventTags] = useState([]);
    const [eventMessage, setEventMessage] = useState("message");
    const [eventEntity, setEventEntity] = useState({
        title: "Title",
        description: "Description",
        location: "Location"
    });

    const { event_id } = useParams();

    useEffect(() => {
        createAPIEndpoint("events/" + event_id)
            .get()
            .then((result) => {
                setEventEntity({
                    ...eventEntity,
                    "title": result.data.event_name.S,
                    "description": result.data.event_description.S,
                    "location": result.data.event_location.S
                });

                setSelectedDateTime(Dayjs(result.data.event_date_time.S));

                setSelectedFile(result.data.event_photo.S);

                setEventTags(result.data.event_tags.SS);
            })
            .catch((err) => {
                // handle error
                console.log(err);
            });
    }, []);

    const handleDateTimeChange = (newDateTime) => {
        setSelectedDateTime(newDateTime);
    };

    const handlePhotoChange = (event) => {
        var file = event.target.files[0];
        setSelectedRawFile(file);
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onloadend = function(e) {
            setSelectedFile(reader.result);
        };
    };

    const handleSave = (e) => {
        e.preventDefault();

        const payload = {
            ...eventEntity,
            "notification": notification,
            "message": notification ? eventMessage: "",
            "dateTime": selectedDateTime.format(),
            "photo": selectedRawFile,
            "eventTags": eventTags,
            "photo_url": selectedRawFile ? "" : selectedFile // when a user uploads their own file, the selectedFile is too large to be sent to the backend.
                                                            // so we send an empty string instead, and the backend will use the photo instead.
        }

        createAPIEndpoint("events/edit/" + event_id, "", "", 'multipart/form-data')
            .post(objToFormData(payload))
            .then((_result) => {
                navigate("/student-group/" + group_id);
            })
            .catch((err) => {
                // handle error
                console.log(err);
            });
    }

    const handleDelete = (e) => {
        e.preventDefault();

        createAPIEndpoint("events/" + event_id, {"notification": notification, "message": notification ? eventMessage: ""})
            .delete()
            .then((_result) => {
                navigate("/student-group/" + group_id);
            })
            .catch((err) => {
                // handle error
                console.log(err);
            });
    }

    const handleNotification = (e) => {
        setNotification(e.target.checked);
    }

    const handleEventEntityChange = (event) => {
        setEventEntity({
            ...eventEntity,
            [event.target.name]: event.target.value
        });
    };

    const handleMessageChange = (event) => {
        setEventMessage(event.target.value);
    }

    return(
        <div style={{flex: "10", display: "flex", flexDirection: "column"}} >
            <DarkButton style={{width: "5%"}} onClick={() => {navigate("/student-group/" + group_id)}}>Back</DarkButton>
            <div style={{display: "flex", flexDirection: "column", justifyContent: "center",flex: "10" }}>
                <form>
                    <FlexColumn>
                        <UploadPicture selectedFile={selectedFile} /**onChange={setSelectedFile}*/ onChangeRaw={handlePhotoChange}/>
                        <Input type={"text"} id="title" name="title" value={eventEntity.title} onChange={handleEventEntityChange}/>
                        <br/>

                        <Input type={"text"} id="location" name="location" value={eventEntity.location} onChange={handleEventEntityChange}/>
                        <br/>

                        <EventTagSelects tags={eventFilterOptions} selectedTags={eventTags} setSelectedTags={setEventTags} />

                        <BasicDateTimePicker id="datetime" selectedDateTime={selectedDateTime} handleDateTimeChange={handleDateTimeChange}/>

                        <LargeTextArea id="description" name="description" minRows={4} value={eventEntity.description} onChange={handleEventEntityChange}/>

                        <FormControlLabel sx={{marginLeft: "5pt"}} control={<Checkbox onChange={handleNotification}/>} label="Notify users of change" />
                        <br/>

                        {
                            notification && <LargeTextArea 
                                    id="message" name="message" minRows={4} onChange={handleMessageChange}
                                    placeholder="Enter a message to notify users of the change"
                                />
                        }

                        <Box sx={{ display: 'flex', flexDirection: 'row' }}>
                            <DarkButton id="save" name="save" type="submit" onClick={handleSave}>SAVE</DarkButton>
                            <DeleteButton id="delete" name="delete" type="submit" onClick={handleDelete}>DELETE</DeleteButton>
                        </Box>
                        
                    </FlexColumn>
                </form>
            </div>
        </div>
    )
}