import React, { useContext, useEffect, useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import { DarkButton, FlexColumn, Input, LargeTextArea, UploadPicture } from '../../style/style';
import { createAPIEndpoint, isLoggedOut, objToFormData } from '../../utils/api';
import AppContext from '../../utils/context/context';
import NotFound from '../Error/NotFound';

export default function EditSG() {
    const { group_id } = useParams();
    const navigate = useNavigate();
    const [group, setGroup] = useState({});
    const [selectedFile, setSelectedFile] = useState(null);
    const [selectedRawFile, setSelectedRawFile] = useState(null);
    
    useEffect(() => {
        createAPIEndpoint("studentGroups/" + group_id)
        .get()
        .then(res => {
            setGroup(res.data)
            setSelectedFile(res.data.group_photo?.S)
        })
    }, [group_id])

    const handlePhotoChange = (event) => {
        var file = event.target.files[0];
        setSelectedRawFile(file);
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onloadend = function(e) {
            setSelectedFile(reader.result);
        };
    };

    const editClick = (e) => {
        e.preventDefault();

        const data = {
            description: e.target.description.value,
            group_name: e.target.name.value,
            photo: selectedRawFile,
            photo_url: selectedRawFile ? "" : selectedFile,
        };

        createAPIEndpoint("studentGroups/edit/" + group_id, {"group_id": group_id}, "", 'multipart/form-data' )
        .post(objToFormData(data))
        .then(res => {
            if(res.status === 200){
                navigate("/student-group/" + group_id)
            }
        })
    }

    // need to add check if the user is in the student group not just them being logged in to route to 404
    if(isLoggedOut()){
        return <NotFound />;
    }
    
    return(
        <form style={{flex: "10"}} onSubmit={editClick}>
            <DarkButton onClick={() => {navigate("/student-group/" + group_id)}}>Back</DarkButton>
            <FlexColumn>
                <UploadPicture selectedFile={selectedFile} onChangeRaw={handlePhotoChange}/>

                <Input type={"text"} id="name" name="name" defaultValue={group.group_name?.S}/>
                <br/>

                <LargeTextArea id="description" minRows={4} defaultValue={group.description?.S}/>
                <br/>

                <DarkButton type="submit">SAVE</DarkButton>
            </FlexColumn>
        </form>
    )
}