import { Container } from '@mui/system';
import React, { useEffect, useState } from 'react';
import { Navigate, useNavigate } from 'react-router-dom';
import { DarkButton, FlexColumn, FlexRow, Title, EventCards, LightButton, ProfileIcon, Loading, MembersDropdown} from '../../style/style';
import { createAPIEndpoint, getEmail, isLoggedOut } from '../../utils/api';
import { useParams } from 'react-router-dom';

export default function StudentGroup() {
    const { group_id } = useParams(); // TODO: use group id to pull data from backend
    const [followStatus, setFollowStatus] = useState(false);
    const [group, setGroup] = useState(null);
    const [ecView, setEcView] = useState(null);
    const navigate = useNavigate();

    useEffect(() => {
        createAPIEndpoint("studentGroups/" + group_id)
        .get()
        .then(res => {
            setGroup(res.data)
        })
        .catch(err => {
            // TODO: handle error
            console.log(err);
        });

        createAPIEndpoint("followGroup/" + group_id)
            .get()
            .then((res) => {
                if (res.data.Items?.filter(follower => {return follower.email.S === getEmail()}).length > 0) {
                    setFollowStatus(true);
                }
            })
            .catch(err => {
                // TODO: handle error
                console.log(err);
            });    

        createAPIEndpoint("memberGroups/" + getEmail())
        .get()
        .then(res => {
            setEcView( res.data.Items.filter(d => { return d.group_id?.N === group_id}).length > 0);
        })
        .catch(err => {
            // TODO: handle error
            console.log(err);
        });

    }, [group_id])
    
    const followClick = () => {
        const data = {
            "follower_email": getEmail()
        };

        if(followStatus){
            createAPIEndpoint("followGroup/" + group_id + "/unfollow", data)
            .delete()
            .then(res => {
                setFollowStatus(false)
            })
            .catch(err => {
                console.log(err);
            })
        } else{
            createAPIEndpoint("followGroup/" + group_id + "/follow")
            .post(data)
            .then(res => {
                setFollowStatus(true)
            })
            .catch(err => {
                console.log(err);
            })
        }
    }

    return(
        group === null | ecView === null ? <Loading />:
        <div style={{flex: "10"}}>
        <FlexColumn>
            <Container sx={{ py: 4 }}>
                <FlexRow style={{width: "100%"}}>
                    <FlexColumn style={{flex: "0"}}>
                        <ProfileIcon src={group.group_photo?.S} alt={"profile"} width={200}  height={200}/>
                        {ecView ? <DarkButton size='sm' onClick={() => {navigate("edit")}}>EDIT</DarkButton>: null}
                    </FlexColumn>

                    <div style={{marginLeft: "10pt"}}>
                        <Title>{group.group_name?.S}</Title>
                        <div>{group.description?.S}</div>
                            {ecView ? 
                                <div>
                                    <FlexRow>
                                        <MembersDropdown group_id={group_id} />

                                        <LightButton size='sm' onClick={() => {navigate("event/create");}}>CREATE EVENT</LightButton>
                                    </FlexRow>
                                </div>:
                                isLoggedOut() ? null:
                                <DarkButton style={{marginLeft: 0}} size='sm' onClick={followClick}>{followStatus ? "UNFOLLOW": "FOLLOW"}</DarkButton>
                            }
                    </div>
                </FlexRow>
            </Container>
            
            <FlexColumn>
                <Title>Hosted Events!</Title>
                <Container sx={{ py: 4 }} maxWidth="sm" maxheight="sm">
                    <EventCards
                        events={group.events}
                        ecView = {ecView}
                    />
                </Container>
            </FlexColumn>
        </FlexColumn>
        </div>
    )
}