import React, { useState, useEffect } from 'react';
import Container from '@mui/material/Container';
import { SearchBar, Filter, FeedTypeSelector, FilterButton, EventCards } from '../../style/style';
import { useNavigate } from 'react-router-dom';
import { createAPIEndpoint, getEmail } from '../../utils/api';
import { eventFilterOptions } from '../../utils/constants';

export default function Home() {
    const [selectedFilterOptions, setSelectedFilterOptions] = useState([]);
    const [showFilters, setShowFilters] = useState(false);
    const [feedType, setFeedType] = useState('general');
    const [page, setPage] = useState(1);
    const [events, setEvents] = useState([]);

    const navigate = useNavigate();

    const PAGE_SIZE = 10;

    const getEvents = (feed = "") => {
        const params = {};
        if (feed === 'following') {
            params.following_email = getEmail();
        }
        if (selectedFilterOptions.length > 0) {
            params.filter = selectedFilterOptions;
        }

        createAPIEndpoint("events?page=" + page + "&limit=" + PAGE_SIZE, "", params ? params : "")
            .get()
            .then((res) => {
                setEvents(res.data)
            })
            .catch((err) => {
                // TODO: handle error
                console.log(err);
            });
    }

    useEffect(() => {
        getEvents();
    }, [selectedFilterOptions]);

    const handleFilterChange = (faculty, checked) => {
        setSelectedFilterOptions((prevSelectedFaculties) =>
            checked
                ? [...prevSelectedFaculties, faculty]
                : prevSelectedFaculties.filter((selected) => selected !== faculty),
            );
    }

    // TODO: Get the backend to return proper event cards based on the filters
    const toggleFilters = () => {
        setShowFilters(!showFilters);
    }

    // if we switch between general and following, get the events from the backend
    const handleFeedTypeChange = (newFeedType) => {
        setFeedType(newFeedType);
        getEvents(newFeedType);
    }

    return(
        <Container 
            sx={{ 
                display: 'flex',
                flexDirection: 'row',
            }}
        >
            {/* FILTER COLUMN */}
            <Container sx={{ py:4, width: "20%" }}>
                <FilterButton
                    showFilters={showFilters}
                    selectedFilterOptions={selectedFilterOptions}
                    toggleFilters={toggleFilters}
                />
                {showFilters && (
                    <Filter 
                        filterOptions={eventFilterOptions}
                        selectedFilterOptions={selectedFilterOptions}
                        onFilterChange={handleFilterChange}
                    />
                )}
            </Container>

            {/* MAIN EVENTS COLUMN */}
            <Container sx={{ py:4 }} maxWidth="md">

                {/* SEARCH BAR */}
                <Container sx={{ py: 0 }} maxWidth="md">
                    <SearchBar onSearch={(searchText) => navigate(`/home/search?search=${searchText}`)}/>
                </Container>

                {/* FEED SELECTOR */}
                <Container sx={{ display: 'flex', justifyContent: 'center' }}>
                    <FeedTypeSelector 
                        feedType={feedType}
                        onChange={handleFeedTypeChange}
                    />
                </Container>

                {/* EVENT CARDS */}
                <Container sx={{ py: 4 }} maxWidth="md">
                    <EventCards
                        events={events}
                    />
                </Container>
            </Container>
        </Container>
    )
}
