import './App.css';
import Login from './components/Login/Login';
import Register from './components/Register/Register';
import Home from './components/Home/Home';

import { BrowserRouter, Navigate, Route, Routes } from 'react-router-dom';
import Header from './components/Header/Header';
import AppContext from './utils/context/context';
import { useState } from 'react';
import EditProfile from './components/Profile/EditProfile';
import User from './components/Profile/User';
import Profile from './components/Profile/Profile';
import Follow from './components/Follow/Follow';
import ECRegister from './components/Register/ECRegister';
import NotFound from './components/Error/NotFound';
import ECRequests from './components/Admin/ECRequests';
import StudentGroup from './components/Profile/StudentGroup';
import Event from './components/Event/Event';
import Members from './components/Profile/Members';
import EditEvent from './components/Event/EditEvent';
import EditSG from './components/Profile/EditSG';
import CreateEvent from './components/Event/CreateEvent';
import Search from './components/Home/Search';

function App() {
  // still have context in case we want to use it for like when a user selects a student group or user and then set it as selected to use in api call
  const user = {
    photo: "https://uevents-s3.s3.us-east-2.amazonaws.com/default_event_photo.png"
  }
  const [context, setContext] = useState(user);

  return (
    <AppContext.Provider value={[context, setContext]}>
      <div style={{height: "100vh", display: "flex", flexDirection: "column"}}>
        <BrowserRouter>
        <Header />
        <Routes>
          <Route path="/" element={<Navigate to="/home" />}/>
          <Route path="/login" element={<Login />}/>
          <Route path="/register" element={<Register />}/>
          <Route path="/home" element={<Home />}/>
          <Route path={"/home/search"} element={<Search />}/>
          <Route path={"/user/:user_id"} element={<User />}/>
          <Route path={"/student-group/:group_id/members"} element={<Members />}/>
          <Route path={"/student-group/:group_id/edit"} element={<EditSG />}/>
          <Route path={"/student-group/:group_id"} element={<StudentGroup />}/>
          <Route path={"/student-group/:group_id/event/create"} element={<CreateEvent />}/>
          <Route path={"/student-group/:group_id/event/edit/:event_id"} element={<EditEvent />}/>
          <Route path={"/event/:event_id"} element={<Event />}/>
          <Route path={"/profile"} element={<Profile />}/>
          <Route path={"/profile/edit"} element={<EditProfile />}/>
          <Route path={"/profile/ec-register"} element={<ECRegister />}/>
          <Route path={"/follow"} element={<Follow />}/>
          <Route path={"/profile/ec-requests"} element={<ECRequests />}/>

          <Route path='*' element={<NotFound />}/>
        </Routes>
        </BrowserRouter>
      </div>
    </AppContext.Provider>
  );
}

export default App;
