import { Checkbox, FormControlLabel } from '@mui/material';
import { Container } from '@mui/system';
import React, { useState } from 'react';
import { useNavigate } from 'react-router';
import { DarkButton, BorderContainer, FlexColumn, FlexColumnCentered, Input, LargeTextArea, LightButton, NewGroupCheck, Title } from '../../style/style';
import { createAPIEndpoint, isLoggedOut } from '../../utils/api';
import NotFound from '../Error/NotFound';

export default function ECRegister() {
    const [check, setCheck] = useState(false)
    const navigate = useNavigate();

    const handleCheck = (e) => {
        setCheck(e.target.checked)
        // console.log(e)
    }

    const handleRequest = (e) => {
        // backend sent request for EC
        e.preventDefault();

        let payload = {
            "email": e.target.email.value,
            "description": e.target.description.value,
            "group_name": e.target.student_group.value,
        }

        if(e.target.newGroup.checked){
            createAPIEndpoint("requests/")
            .post(payload)
            .then(res => {
                if(res.status === 200){
                    navigate("/home")

                }
            })
                   
        } else{
            payload = {
                ...payload,
                "group_id": e.target.id.value

            }
            createAPIEndpoint("requests/")
            .post(payload)
            .then(res => {
                if(res.status === 200){
                    navigate("/home")
                }
            })
        }
    }

    if(isLoggedOut()){
        return <NotFound />;
    }

    return(
        <FlexColumnCentered>
            <Title>Registration for Event Coordinators</Title>
            Please fill out fields below to register as an Event Coordinator
            <br/>
            <br/>
            <BorderContainer onSubmit={handleRequest}>
                <FlexColumn>
                    <Input type={"text"} id="email" name="email" placeholder={"Enter email"} required/>
                    <br/>
                    
                    <Input type={"text"} id="student_group" name="student_group" placeholder={"Enter student group name"} required/>
                    <br/>

                    <Input type={"text"} id="id" name="id" disabled={check} placeholder={"Enter group id"} required={!check}/>

                    <NewGroupCheck name ="newGroup" id="newGroup" control={<Checkbox onChange={handleCheck}/>} label="Create new student group" />
                    
                    <LargeTextArea style={{width: "100%"}} id="description" minRows={4} placeholder={"Description message for admin"}/>

                    <Container>
                        <DarkButton type="submit">SUBMIT</DarkButton>
                        <LightButton type="button" onClick={() => {navigate("/profile")}}>CANCEL</LightButton>
                    </Container>

                </FlexColumn>
            </BorderContainer>
        </FlexColumnCentered>
    )
}